.notifications {
  max-width: 720px;

  &__inner {
    padding-top: 30px;
  }

  &__title {
    margin: 0 0 10px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    min-width: 720px;
  }

  &__item {
    position: relative;
    min-width: 720px;
    min-height: 75px;

    &:not(:last-child){
      border-bottom: 1px solid #ccc;
    }

    &:hover{
      background-color: #D9F3FF;
    }
  }

  &__link {
    min-width: 720px;
    min-height: 75px;
  }

  &__naming{
    margin: 15px 15px 0 15px;
    font-size: 16px;
    font-weight: 800;
    line-height: 24px;
    color: var(--dark-color);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 600px;
  }

  &__text {
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    color: var(--dark-color);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin: 0 15px 15px;
    max-width: 600px;
  }

  &__date {
    position: absolute;
    top: 15px;
    right: 15px;
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    color: var(--grey-color);
  }
}

.message {

  &__inner {
    padding-top: 30px;
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    color: var(--dark-color);
    margin: 0 0 10px;
  }

  &__link {
    display: block;
    font-size: 16px;
    font-weight: 500;
    color: var(--accent-color-2);
    line-height: 22px;
    margin-bottom: 10px;

    &:hover{
      color: var(--hover-color);
    }
  }
}
