.address {

  &__inner {
    padding-top: 30px;
  }

  &__title {
    margin: 0 0 20px;
    font-size: 32px;
    font-weight: 800;
    color: var(--dark-color);
    line-height: 36px;
  }

  &__text {
    margin: 0;
    font-size: 18px;
    font-weight: 500;
    color: var(--dark-color);
    line-height: 24px;
    max-width: 518px;
  }

  &__btn {
    padding: 0 17px;
    min-height: 44px;
    border-radius: 12px;
    background-color: var(--dark-color);
    color: var(--light-color);
    font-size: 16px;
    font-weight: 500;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 8px;
  }

  &__item {
    background-color: #F2F1F0;
    border-radius: 24px;
    padding: 10px 16px 24px;
  }

  &__list-title {
    font-size: 18px;
    font-weight: 900;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__list-text {
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
    color: var(--dark-color);
  }
}

.address-modal {
  display: grid;
  grid-template-columns: repeat(2, 450px);

  &__title {
    font-size: 26px;
    font-weight: 700;
    line-height: 30px;
    color: var(--dark-color);
  }

  &__description{
    padding-right: 40px;
  }

  &__input {
    background-color: #F2F1F0;
    min-height: 40px;
    width: 100%;
    border-radius: 12px;
    padding: 0 40px;
    background-image: url("../img/search.svg");
    background-repeat: no-repeat;
    background-size: 20px;
    background-position-x: 10px;
    background-position-y: 50%;

    &::placeholder{
      font-size: 15px;
      font-weight: 500;
    }
  }

  &__type {
    display: flex;
    align-items: stretch;
    gap: 5px;
  }

  &__type-btn {
    padding: 0 15px;
    min-height: 36px;
    border-radius: 12px;
    background-color: #F2F1F0;
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
  }

  &__type-btn--active {
    background-color: var(--dark-color);
    color: var(--light-color);
  }

  &__subtitle{
    font-size: 16px;
    font-weight: 800;
    line-height: 20px;
    color: var(--dark-color);
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: stretch;
    gap: 5px;
  }

  &__content-type {
    background-color: #F2F1F0;
    max-width: 130px;
    min-height: 44px;
    border-radius: 12px;
    padding: 10px 10px 12px 15px;
    border: 3px solid transparent;

    &:focus{
      border-color: var(--accent-color-2);
    }

    &::placeholder{
      font-size: 15px;
      font-weight: 500;
    }
  }

  &__textarea {
    resize: none;
    background-color: #F2F1F0;
    width: 100%;
    min-height: 85px;
    border-radius: 12px;
    padding: 10px 39px 12px 15px;
    border: 3px solid transparent;

    &:focus{
      border-color: var(--accent-color-2);
    }

    &::placeholder{
      font-size: 15px;
      font-weight: 500;
    }
  }

  &__alert {
    display: block;
    font-size: 13px;
    font-weight: 400;
    color: var(--grey-color);
    line-height: 18px;

    & a{
      color: var(--grey-color);
      text-decoration: underline;
    }
  }

  &__btn {
    padding: 0 17px;
    min-height: 44px;
    border-radius: 12px;
    font-size: 16px;
    font-weight: 500;
    background-color: var(--accent-color-2);
    color: var(--light-color);
  }

}
