.__select {
  position: relative;
  height: 40px;
  min-width: 300px;
  user-select: none;

  &[data-state="active"] {
    .__select__title {
      &::before {
        transform: translate(-3px, -50%) rotate(-45deg);
      }

      &::after {
        transform: translate(3px, -50%) rotate(45deg);
      }
    }

    .__select__content {
      display: flex;
      opacity: 1;
    }

    .__select__label + .__select__input + .__select__label {
      max-height: 40px;
      border-top-width: 1px;
    }
    .__select__title{
      background: var(--light-color);
      border-color: #80D4FF;
    }
  }
}
.__select__title {
  display: flex;
  align-items: center;

  width: 100%;
  height: 100%;
  padding: 8px 16px;
  border-radius: 6px;
  background: #F2FBFF;
  border: 3px solid transparent;
  cursor: pointer;
  color: #999B99;
  font-size: 16px;
  font-weight: 500;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 16px;
    display: block;
    width: 10px;
    height: 2px;
    transition: all 0.3s ease-out;
    background-color: #333333;
    transform: translate(-3px, -50%) rotate(45deg);
  }
  &::after {
    transform: translate(3px, -50%) rotate(-45deg);
  }

  &:hover {
    background: var(--light-color);
  }
}
.__select__content {
  position: absolute;
  top: 43px;
  left: 0;
  display: none;
  flex-direction: column;
  width: 100%;
  max-height: 355px;
  overflow-y: scroll;
  background-color: #ffffff;
  border-top: none;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 17px 0px;
  transition: all 0.3s ease-out;

  opacity: 0;
  z-index: 8;
}
.__select__input {
  display: none;
  &:disabled + label {
    opacity: 0.6;
    pointer-events: none;

    &:hover{
      background: var(--light-color) !important;
    }
  }
}
.__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 40px;
  max-height: 0;
  padding: 0 16px;

  transition: all 0.2s ease-out;

  cursor: pointer;

  overflow: hidden;

  &:hover {
    background-color: #F2F1F0 !important;
  }
}
