.cart {

  &__inner {
    padding-top: 30px
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    color: var(--dark-color);
    margin: 0;

    & span{
      color: var(--grey-color);
    }
  }

  &__container {
    display: grid;
    grid-template-columns: 1fr 350px;
    gap: 30px;
    align-items: flex-start;
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  &__menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
  }

  &__menu-btn {
    color: var(--accent-color-2);
    font-size: 15px;
    font-weight: 400;

    &:hover{
      color: var(--hover-color);
    }

    & span{
      &::before{
        content: '(';
      }
      &::after{
        content: ')';
      }
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__store {
    margin-bottom: 20px;

    & div label{
      font-size: 15px;
      font-weight: 500;
      color: var(--dark-color);
    }

    & div label a{
      font-size: 21px;
      font-weight: 700;
      line-height: 26px;
      color: var(--dark-color);

      &:hover{
        color: var(--hover-color);
      }
    }

    & div label svg{
      width: 16px;
      height: 16px;
      fill: #FFB021;
      margin: 0 5px;
    }
  }

  &__review {
    margin-right: 5px;
  }

  &__list-num {
    color: var(--grey-color);
    &::before{
      content: '(';
    }
    &::after{
      content: ')';
    }
  }

  &__store-link {
    display: block;
    margin-top: 10px;
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: var(--dark-color);
    width: fit-content;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__product {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 30px;

    & .image{
      border-radius: 12px;
      width: 112px;
      height: 112px;
    }
  }

  &__product-item {
    display: flex;
    align-items: flex-start;
  }

  &__product-description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    padding-left: 20px;
  }

  &__product-title {
    display: block;
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: var(--dark-color);

    &:hover{
      color: var(--hover-color);
    }
  }

  &__product-property {
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: var(--grey-color);
  }

  &__product-link {
    display: block;
    font-size: 15px;
    font-weight: 400;
    line-height: 20px;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: var(--accent-color-2);

    &:hover{
      color: var(--hover-color);
    }
  }

  &__product-price {
    display: block;
    font-size: 18px;
    font-weight: 800;
    color: var(--dark-color);
    line-height: 24px;
    margin-bottom: 8px;
  }

  &__product-value-menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    background-color: #F2F1F0;
    width: 135px;
    height: 44px;
    border-radius: 12px;
    overflow: hidden;

    & span{
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      font-weight: 500;
    }
  }

  &__product-value-btn {
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover{
      background-color: #ebeae8;
    }

    &:active{
      background-color: #e3e2e1;
    }
  }

  &__description {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
    min-width: 350px;
    box-shadow: rgba(255, 255, 255, 0.05) 0px 1px 3px 0px, rgba(0, 0, 0, 0.12) 0px 4px 24px 0px;
    padding: 20px 24px 32px;
    border-radius: 20px;
  }

  &__description-item {
    display: flex;
    align-items: flex-end;
    gap: 2px;

    & span{
      white-space: nowrap;
    }
  }

  &__description-border {
    display: block;
    width: 100%;
    height: 1px;
    border-bottom: 1px dotted var(--grey-color);
    margin-bottom: 3px;
  }

  &__description-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 52px;
    width: 100%;
    border-radius: 16px;
    background-color: #965EEB;
    color: var(--light-color);
    font-size: 16px;
    font-weight: 500;
    margin-top: 10px;

    &:hover{
      background-color: #8c4fe8;
    }

    &:active{
      background-color: #8144db;
    }
  }

  .nav {

    &__list {
      display: flex;
      align-items: center;
      gap: 25px;
    }

    &__btn {
      width: 20px;
      height: 20px;

      & svg{
        fill: #B8B8B8;

        &:hover{
          fill: var(--dark-color);
        }
      }
    }
  }
}
