.promo {

  &__inner {
    padding-top: 30px;
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    color: var(--dark-color);
    margin: 0 0 20px;
  }

  &__text {
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 17px;
    min-height: 40px;
    width: fit-content;
    background-color: var(--accent-color-2);
    color: var(--light-color);
    font-size: 16px;
    font-weight: 500;
    border-radius: 6px;
    margin: 20px 0 50px;
  }

  .tabs__nav {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #E3E2E1;
    padding-bottom: 15px;
    gap: 30px;
    width: 1000px;
  }
  .tabs__nav-btn {
    position: relative;
    font-size: 25px;
    font-weight: 700;
    line-height: 26px;
    color: var(--grey-color);
  }
  .tabs__nav-btn sup {
    color: var(--grey-color);
  }
  .tabs__nav-btn--active{
    color: var(--dark-color);
  }
  .tabs__nav-btn--active::after{
    content: '';
    position: absolute;
    bottom: -17px;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: var(--dark-color);
  }
  .tabs__panel{
    padding-top: 20px;
  }
}

.promocode {

  &__inner {
    padding: 30px;
  }

  &__title {
    font-size: 32px;
    font-weight: 800;
    line-height: 36px;
    color: var(--dark-color);
    margin: 0 0 30px;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    gap: 50px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    row-gap: 50px;
  }

  &__container-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__container-title {
    font-size: 20px;
    font-weight: 800;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 15px;
  }

  &__input {
    min-height: 40px;
    width: 100%;
    background-color: #F2F1F0;
    border-radius: 6px;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 500;
    color: var(--dark-color);
    line-height: 22px;

    &:focus{
      box-shadow: rgb(128, 212, 255) 0px 0px 0px 3px;
      background-color: var(--light-color);
    }
  }

  &__advice {
    display: block;
    font-size: 14px;
    font-weight: 400;
    color: var(--grey-color);
    line-height: 18px;
    margin-top: 5px;
  }

  &__link {
    font-size: 16px;
    font-weight: 500;
    color: var(--accent-color-2);
    line-height: 22px;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__rebate {
    margin-top: 20px;
  }

  &__subtitle {
    font-size: 16px;
    font-weight: 800;
    color: var(--dark-color);
    line-height: 22px;
    margin-bottom: 5px;
  }

  &__rebate-content {
    display: flex;
    align-items: stretch;
    gap: 5px;
    max-width: 205px;
    min-width: 205px;
    position: relative;
  }

  &__rebate-btn {
    position: relative;
    background-color: #F2F1F0;
    border-radius: 6px;
    min-width: 72px;
    min-height: 40px;
    text-align: left;
    padding: 0 15px;
    font-size: 16px;
    font-weight: 600;
    color: var(--dark-color);

    &::after{
      content: '';
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      background-image: url("../img/arrow-2.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__value {
    margin-top: 20px;
    margin-bottom: 20px;

    & .promocode__input{
      max-width: 140px;
    }
  }

  div label{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 25px;

    &::before{
      position: absolute;
      left: 0;
      top: 5px;
    }
  }

  &__container-btn {
    padding: 0 17px;
    min-height: 40px;
    border-radius: 6px;
    background-color: #F2EFE9;
    margin-top: 20px;
    font-size: 16px;
    font-weight: 500;
    color: var(--dark-color);
  }

  &__date {}

  &__description {
    min-width: 350px;
    background-color: var(--light-color);
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 3px 0px, rgba(0, 0, 0, 0.12) 0px 4px 24px 0px;
    padding: 20px 24px 32px;
  }

  &__description-title {
    font-size: 16px;
    font-weight: 800;
    color: var(--dark-color);
    line-height: 22px;
    margin-bottom: 20px;
  }

  &__description-text {
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    color: var(--dark-color);
    line-height: 22px;
  }

  .special-description-text::before{
    content: '- ';
  }

  &__menu {
    display: flex;
    align-items: stretch;
    gap: 8px;
    padding-top: 50px;
    margin-bottom: 10px;
  }

  &__menu-btn {
    min-height: 40px;
    padding: 0 17px;
    border-radius: 6px;
    font-size: 16px;
    font-weight: 600;

    &:nth-child(1){
      background-color: var(--accent-color-2);
      color: var(--light-color);
    }
    &:nth-child(2){
      background-color: #CFEDFF;
      color: var(--accent-color-2);
    }
  }
}
