.card {
  padding: 8px;
  border-radius: 8px;
  background: var(--light-color);
  cursor: pointer;
  transition: .2s linear;

  &:hover{
    background: #F5F5F5;
    transition: .2s linear;
  }
}
.card__picture {
  border-radius: 5px;
  overflow: hidden;
  max-height: 237px;

  & .image{
    max-height: 237px;
    border-radius: 5px;
    overflow: hidden;
  }
}
.card__description{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 5px;
}
.card__content {
  position: relative;
}
.card__description-title {
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  color: var(--accent-color-2);
  max-width: 80%;
  text-align: left;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card__description-price {
  display: flex;
  align-items: center;
  gap: 5px;
  color: var(--dark-color);
  font-size: 16px;
  line-height: 20px;
  margin: 5px 0 0;

  strong{
    max-width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.card__description-price-old{
  display: flex;
  align-items: center;
  gap: 10px;
}
.card__description-price-old span {
  color: #858585;
  text-decoration: line-through;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  max-width: 100px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card__description-discount {
  text-decoration: none !important;
  color: #ff4053 !important;
  max-width: 50px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  &::after{
    content: '%';
  }
}
.card__description-text {
  color: #858585;
  font-size: 16px;
  font-weight: 400;
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card__favorite {
  position: absolute;
  top: 0;
  right: 0;
}
