.main__content{
  overflow: hidden;
}

.favourite {

  &__inner {
    padding-top: 20px;
  }

  &__title {
    font-size: 32px;
    font-weight: 800;
    color: var(--dark-color);
    line-height: 36px;
    margin: 0 0 20px;
  }

  &__category {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    gap: 5px;
    margin-bottom: 20px;
    padding-left: 10px;
  }

  &__category-btn {
    padding: 6px 16px 8px;
    border-radius: 6px;
    background-color: #EBEBEB;
    font-size: 13px;
    font-weight: 400;
    color: var(--dark-color);
    white-space: nowrap;
  }

  &__sort {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
    padding-left: 10px;

    & svg{
      width: 16px;
      height: 16px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}
.favourite-tab .tabs__nav {
  display: flex;
  align-items: center;
  border-bottom: 2px solid #E3E2E1;
  padding-bottom: 15px;
  gap: 30px;
  width: 1000px;
}
.favourite-tab .tabs__nav-btn {
  position: relative;
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  color: var(--grey-color);
}
.favourite-tab .tabs__nav-btn sup {
  color: var(--grey-color);
}
.favourite-tab .tabs__nav-btn--active{
  color: var(--dark-color);
}
.favourite-tab .tabs__nav-btn--active::after{
  content: '';
  position: absolute;
  bottom: -17px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: var(--dark-color);
}
.favourite-tab .tabs__panel{
  padding-top: 20px;
}
.compilation {

  &__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
    padding-bottom: 150px;
  }

  &__item{
    position: relative;
  }

  &__link {

    .image{
      max-width: 195px;
      max-height: 195px;
      min-width: 195px;
      min-height: 195px;
    }
  }

  &__menu {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 20px;
    padding-top: 10px;
  }

  &__title {
    margin: 0;
    color: var(--dark-color);
    font-size: 15px;
    font-weight: 600;
    line-height: 22px;
  }

  &__text {
    margin: 0;
    color: var(--dark-color);
    font-size: 13px;
    font-weight: 400;
    line-height: 22px;
  }

  &__btn {
    position: relative;
    z-index: 5px;
    width: 20px;
    height: 20px;
  }
}
.favourite-search {

  &__menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__menu-content {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__menu-btn {
    min-height: 32px;
    padding: 0 13px;
    border-radius: 6px;
    background-color: #F2EFE9;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 400;
  }

  &__menu-text {
    display: block;
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
    line-height: 18px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__description {
    display: flex;
    align-items: flex-start;
    gap: 15px;
  }

  &__link {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: var(--accent-color-2);
    line-height: 24px;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__description-text {
    display: block;
    font-size: 16px;
    font-weight: 400;
    color: var(--grey-color);
    line-height: 24px;
  }
}
.favourite-profile {

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 700px);
  }

  &__user {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 10px;

    & .image{
      border-radius: 50%;
    }
  }

  &__description-name {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__description-num {
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    color: var(--dark-color);
  }
}
