.sort {
  user-select: none;

  &__inner {
    padding-top: 50px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 30px;
  }

  &__title {
    color: #5c5c5c;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    margin-bottom: 8px;
  }

  &__input {
    width: 100%;
    min-height: 30px;
    border-radius: 3px;
    border: 1px solid #b8b8b8;
    padding: 0 8px;
    transition: .2s ease-in-out;
    font-size: 14px;
    font-weight: 500;
    color: var(--dark-color);

    &::placeholder{
      font-size: 15px;
      font-weight: 500;
      color: #B1B1B1;
    }

    &:focus{
      transition: .2s ease-in-out;
      -webkit-box-shadow: 0px 0px 0px 4px #B2E5FF;
      -moz-box-shadow: 0px 0px 0px 4px #B2E5FF;
      box-shadow: 0px 0px 0px 4px #B2E5FF;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
  }

  &__list-2 {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px;
  }

  &__color {
    width: 27px;
    height: 27px;
    border-radius: 50%;
    cursor: pointer;
  }

  &__btn {
    color: var(--accent-color-2);
    font-size: 14px;
    font-weight: 700;
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 25px;
  }

  &__tab {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    min-height: 30px;
    border-radius: 3px;
    border: 1px solid #ccc;
    margin-bottom: 25px;
  }

  &__tab-btn {
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
    min-height: 30px;

    &:not(:last-child){
      border-right: 1px solid #ccc;
    }

    &:hover{
      background: #f1f1f1;
    }

    &:focus{
      transition: .2s ease-in-out;
      -webkit-box-shadow: 0px 0px 0px 4px #B2E5FF;
      -moz-box-shadow: 0px 0px 0px 4px #B2E5FF;
      box-shadow: 0px 0px 0px 4px #B2E5FF;
    }
  }

  &__result {
    width: 100%;
    min-height: 30px;
    padding: 0 15px;
    border-radius: 3px;
    background-color: var(--accent-color-2);
    color: var(--light-color);
    font-size: 15px;
    font-weight: 500;
    margin-top: 30px;
  }
}
.active-color-theme {
  background-image: url("../img/checked.svg");
  background-repeat: no-repeat;
  background-size: 17px;
  background-position: center;
}
.active-tab-btn {
  background: #E0E0E0;

  &:hover{
    background: #E0E0E0;
  }
}
