.top {
  &__inner {
    padding: 35px 0;
  }

  &__title {
    color: var(--dark-color);
    font-size: 25px;
    font-weight: 700;
    line-height: 35px;
  }

  &__subtitle {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    margin: 0 0 20px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: stretch;
    gap: 8px;
    padding-bottom: 30px;
  }

  &__btn {
    padding: 0 17px;
    min-height: 40px;
    border-radius: 6px;
    background-color: #e5dfd3;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 500;
  }
}
