.footer {
  min-width: 300px;
}
.footer__inner {
  padding: 15px;
}
.footer__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: var(--grey-color);
  margin: 14px 0;

  & a{
    color: var(--grey-color);
    text-decoration: underline;
  }
}
.footer__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 3px;
}
.footer__link {
  color: var(--dark-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;

  &:hover{
    color: var(--hover-color);
  }
}
