.filtering {

  &__menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--light-color);
    padding-bottom: 30px;
  }

  &__menu-title {
    font-size: 20px;
    font-weight: 900;
    line-height: 26px;
    color: var(--dark-color);
  }

  &__menu-btn {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: var(--grey-color);
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
    overflow-y: scroll;
    max-height: 600px;
    padding: 15px 15px 15px 0;
  }

  &__tab {
    display: grid;
    grid-template-columns: auto auto auto;
    width: 100%;
    min-height: 30px;
    border-radius: 3px;
    border: 1px solid #ccc;
  }

  &__tab-btn {
    font-size: 15px;
    font-weight: 500;
    line-height: 22px;
    color: var(--dark-color);
    background-color: var(--light-color);

    &:not(:last-child){
      border-right: 1px solid #ccc;
    }

    &:hover{
      background-color: #F5F5F5;
    }

    &:active{
      background-color: #e0e0e0;
    }

    &:focus{
      box-shadow: 0 0 1px 3px rgba(0, 170, 255, .3);
    }

    &-active{
      background-color: #e0e0e0;
    }
  }

  &__title {
    color: rgb(92, 92, 92);
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    margin-bottom: 10px;
  }

  &__grid-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 8px;
  }

  &__content {
    display: flex;
    align-items: stretch;
    border: 1px solid #ccc;

    & input.input-reset:nth-child(1){
      border-right: 1px solid #ccc;
    }
  }

  &__input{
    min-height: 30px;
    padding: 0 10px;
    font-size: 14px;
    font-weight: 500;
    color: var(--dark-color);
  }

  &__footer {
    display: flex;
    align-items: stretch;
    gap: 15px;
    padding: 26px 0 18px;
    background-color: var(--light-color);
  }

  &__footer-btn {
    display: flex;
    align-items: center;
    gap: 5px;
    min-height: 40px;
    background-color: var(--accent-color-2);
    color: var(--light-color);
    font-size: 16px;
    font-weight: 600;
    padding: 0 17px;
    border-radius: 6px;
    width: fit-content;
  }

  &__footer-map {
    background-color: #CFEDFF;
    color: var(--accent-color-2);
  }

  & .date{
    padding: 10px !important;

    & table tbody tr td button.btn-reset{
      min-width: 52px !important;
    }

    & .date__nav{
      padding: 0 18px !important;
    }
  }
}
