.wallet {
  max-width: 720px;

  &__inner {
    padding-top: 30px;
  }

  &__title {
    margin: 0 0 20px;
    font-size: 32px;
    font-weight: 800;
    line-height: 46px;
    color: var(--dark-color);
  }

  &__balance {
    display: grid;
    grid-template-columns: repeat(2, 220px);
    margin-bottom: 50px;
  }

  &__balance-item {
    min-height: 54px;
  }

  &__balance-title {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    color: var(--dark-color);

    & svg{
      width: 20px;
      height: 20px;
      fill: #B8B8B8;
    }
  }

  &__balance-num {
    display: block;
    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
    color: var(--dark-color);
  }

  &__payment{
    position: relative;
    max-width: 205px;
    max-height: 48px;
    margin-bottom: 40px;

    span{
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translateY(-50%);
      font-size: 18px;
      font-weight: 400;
      color: var(--dark-color);
    }
  }

  &__input {
    min-width: 205px;
    min-height: 48px;
    max-width: 205px;
    max-height: 48px;
    border-radius: 6px;
    background-color: #F2F1F0;
    padding: 0 30px 0 20px;
    font-size: 18px;
    border: 3px solid transparent;

    &:hover{
      background-color: #ebeae8;
    }

    &:focus{
      background-color: var(--light-color);
      border-color: #80d4ff;
    }
  }

  &__way-title {
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    color: var(--dark-color);
    margin-bottom: 10px;
  }

  &__way-list {
    display: grid;
    grid-template-columns: repeat(2, 330px);
  }

  &__way-link {
    display: flex;
    align-items: center;
    gap: 10px;
    min-width: 330px;
    min-height: 50px;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__history-list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__history-month {
    display: block;
    font-size: 16px;
    font-weight: 500;
    color: #8B8B8B;
    line-height: 24px;
  }

  &__history-title {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: var(--dark-color);
    line-height: 24px;
  }

  &__history-text {
    display: block;
    font-size: 16px;
    font-weight: 400;
    color: var(--dark-color);
    line-height: 24px;
    margin: 0;
    max-width: 720px;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  &__history-date {
    display: block;
    font-size: 14px;
    font-weight: 400;
    color: var(--grey-color);
    line-height: 24px;
  }
}
.wallet-tab {
  .tabs {
    &__nav {
      display: flex;
      align-items: center;
    }

    &__nav-btn {
      min-height: 36px;
      padding: 0 12px;
      border-radius: 18px;
      font-size: 16px;
      font-weight: 400;
      color: var(--accent-color-2);

      &--active{
        background-color: #F5F5F5 !important;
        font-weight: 700 !important;
        color: var(--dark-color);
      }
    }

    &__panel {
      padding-top: 30px;
    }
  }
}
