.filter {

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 6px;
    padding-left: 6px;
  }

  &__item{
    width: 100%;
  }

  &__link {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--dark-color);
    fill: var(--dark-color);
    width: 100%;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;

    &:hover{
      color: var(--hover-color);
      fill: var(--hover-color);
    }
  }

  &__subtitle {
    display: block;

    color: var(--dark-color);
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    margin: 5px 0;
    padding-left: 6px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    padding-left: 18px;
  }
}

.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;

  & + label {
      display: inline-flex;
      align-items: center;
      user-select: none;
      font-size: 14px;
  }

  & + label::before {
      content: "";
      display: inline-block;
      width: 18px;
      height: 18px;
      flex-shrink: 0;
      flex-grow: 0;
      background: #ebeae8;
      border-radius: 2px;
      margin-right: 0.5em;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 50% 50%;
      transition: 0.2s linear;
  }

  &:checked + label::before {
      background-color: var(--dark-color);
      background-image: url("../img/check.svg");
      background-size: 12px 10px;
      transition: 0.2s linear;
  }

  &:disabled + label::before {
      opacity: 0.5;
  }

  &:not(:disabled):not(:checked) + label:hover::before {
      border-color: #b3d7ff;
  }
}
.radio {
  position: absolute;
  z-index: -1;
  opacity: 0;

  & + label {
      display: inline-flex;
      align-items: center;
      user-select: none;
  }

  & + label::before {
      content: "";
      display: inline-block;
      width: 21px;
      height: 21px;
      flex-shrink: 0;
      flex-grow: 0;
      background: #ebeae8;
      border-radius: 50%;
      margin-right: 0.5em;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 45% 45%;
  }

  &:checked + label::before {
      background-color: var(--dark-color);
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
      background-position: center center;
  }

  &:disabled + label::before {
      opacity: 0.5;
  }
}
