.pro {
  max-width: 720px;

  &__inner {
    padding-top: 30px;
  }

  &__title {
    margin: 0 0 30px;
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    color: var(--dark-color);
  }

  &__text {
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__menu {
    margin: 20px 0 50px;
    display: flex;
    align-items: stretch;
    gap: 5px;
  }

  &__menu-link {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    padding: 0 17px;
    min-height: 40px;
    font-size: 16px;
    font-weight: 600;

    &:nth-child(1){
      background-color: var(--accent-color-2);
      color: var(--light-color);
    }

    &:nth-child(2){
      background-color: #CFEDFF;
      color: var(--accent-color-2);
    }
  }

  .nav {
    margin-bottom: 50px;

    &__list {
      display: flex;
      align-items: center;
    }

    &__link {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 12px;
      min-height: 36px;
      border-radius: 18px;
      color: var(--accent-color-2);
      font-size: 16px;
      font-weight: 400;
    }
  }

  .nav-active {
    background-color: #F5F5F5;
    color: var(--dark-color);
    font-weight: 700;
  }

  .tabs {

    &__nav {
      border-radius: 6px;
      overflow: hidden;
      width: fit-content;
    }

    &__nav-btn {
      min-height: 40px;
      background-color: #F2F1F0;
      padding: 0 17px;
      font-size: 16px;
      font-weight: 400;
      color: var(--dark-color);

      &--active{
        background-color: #DBD3C1;
      }
    }

    &__panel {
      padding-top: 30px;
    }
  }
}
.rate {

  &__title {
    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
    color: var(--dark-color);
    margin-bottom: 30px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 340px);
    gap: 10px;

    & .rate__title{
      font-size: 20px !important;
      font-weight: 700 !important;
      margin-bottom: 10px !important;
    }
  }

  &__menu {
    padding-top: 10px !important;
  }

  &__link {
    font-size: 16px;
    font-weight: 400;
    color: var(--accent-color-2);
  }
}
