.product {
  display: flex;
  align-items: stretch;
  gap: 10px;
  position: relative;
  background-color: var(--light-color);
  padding: 20px;
  border-radius: 8px;
  width: 100%;

  &:hover{
    background-color: #F5F5F5;
  }

  &:hover .product__contact{
    display: grid;
  }

  &__link{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  &__description{
    position: relative;
    max-width: 470px;
  }

  &__content {
    display: flex;
    align-items: stretch;
    gap: 10px;
  }

  &__picture {
    display: flex;
    flex-direction: column;
    min-width: 240px;
    & .image{
      border-radius: 5px;
    }
  }

  &__picture-content {
    display: flex;
    align-items: stretch;
    gap: 8px;
  }

  &__title {
    display: block;
    position: relative;
    z-index: 2;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: var(--accent-color-2);
    white-space: nowrap;
    max-width: 300px;
    overflow: hidden;
    text-overflow: ellipsis;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__price {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 5px;
  }

  &__price-num {
    font-size: 18px;
    font-weight: 700;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__price-delivery {
    display: flex;
    align-items: center;
    position: relative;

    &:hover .product__price-delivery__text{
      display: block;
    }
  }

  &__price-delivery__text{
    display: none;
    position: absolute;
    z-index: 3;
    bottom: 25px;
    right: 50%;
    transform: translateX(50%);
    white-space: nowrap;
    background: #292929;
    color: var(--light-color);
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    padding: 15px 30px;
    border-radius: 12px;

    &::after{
      content: '';
      position: absolute;
      top: 100%;
      right: 50%;
      transform: translateX(50%) rotate(180deg);
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-bottom: 10px solid #292929;
    }
  }

  &__price-svg {
    position: relative;
    z-index: 2;
    width: 16px;
    height: 12px;
    fill: #B8B8B8;
    cursor: pointer;

    &:hover{
      fill: #FFB020;
    }
  }

  &__description-text {
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
  }

  &__text {
    display: flex;
    align-items: center;
    gap: 2px;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    color: var(--grey-color);
    margin: 5px 0;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__text-svg {
    width: 14px;
    height: 14px;
    fill: var(--grey-color);
  }

  &__favorite {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
  }

  &__favorite-svg {
    fill: var(--accent-color-2);
    width: 20px;
    height: 20px;
    transition: .1s ease-in-out;

    &:hover{
      scale: 1.15;
      transition: .1s ease-in-out;
    }
  }

  &__menu {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
    height: fit-content;
  }

  &__company {

    & .image{
      border-radius: 3px;
    }
  }

  &__company-title {
    font-size: 15px;
    font-weight: 500;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__company-rating{
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    font-weight: 500;
    color: var(--dark-color);
    line-height: 22px;
  }

  &__company-star {
    display: flex;
    align-items: center;
    gap: 2px;
  }

  &__company-star-svg {
    width: 12px;
    height: 12px;
    fill: #FFB020;
  }

  &__company-text {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    background: #CCECFF;
    border-radius: 3px;
    color: var(--dark-color);
    text-align: left;
    padding: 0 8px;
    width: fit-content;
    margin-top: 5px;
  }

  &__contact {
    display: none;
    grid-template-columns: repeat(1, 1fr);
    gap: 3px;
    max-width: 200px;
  }

  &__contact-btn {
    background: var(--light-color);
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 0 20px;
    font-size: 14px;
    font-weight: 500;
    color: var(--dark-color);
  }
}
.dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}
