.basic {
  max-width: 720px;

  &__inner {
    padding-top: 30px;
  }

  &__title {
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    color: var(--dark-color);
    margin: 0 0 20px;
  }

  &__user {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__text {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 15px;
    font-weight: 400;
    line-height: 20px;
    color: var(--dark-color);
    margin: 0;
  }

  &__user-picture {
    position: relative;

    & .image{
      border-radius: 50%;
    }
  }

  &__user-set {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 8px;
    right: 8px;
    width: 36px;
    height: 36px;
    background-color: var(--light-color);
    border-radius: 50%;

    .input-reset{
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }

  &__pin {
    margin-bottom: 50px;
  }

  &__panel-title {
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 15px;
  }

  &__pin-alert {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 10px 12px;
    border-radius: 6px;
    background-color: #E6F6FF;
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
  }

  &__confirm {
    margin-bottom: 50px;
  }

  &__confirm-list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
  }

  &__confirm-item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    & .btn-reset{
      color: var(--accent-color-2);
      font-size: 15px;
      font-weight: 400;
    }
  }

  .tabs__nav {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #E3E2E1;
    padding-bottom: 15px;
    gap: 30px;
    width: 1000px;
  }
  .tabs__nav-btn {
    position: relative;
    font-size: 20px;
    font-weight: 700;
    line-height: 26px;
    color: var(--grey-color);
  }
  .tabs__nav-btn sup {
    color: var(--grey-color);
  }
  .tabs__nav-btn--active{
    color: var(--dark-color);
  }
  .tabs__nav-btn--active::after{
    content: '';
    position: absolute;
    bottom: -17px;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: var(--dark-color);
  }
  .tabs__panel{
    padding-top: 20px;
    padding-bottom: 70px;
  }

  &__alert {
    padding: 20px 48px 32px 24px;
    background-color: #D4F0FF;
    border-radius: 24px;
    margin-bottom: 50px;
  }

  &__alert-title {
    font-size: 21px;
    font-weight: 800;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 10px;
  }

  &__alert-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    padding: 5px 17px;
    min-height: 40px;
    border-radius: 12px;
    background-color: var(--light-color);
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    margin-top: 20px;
  }

  &__profile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
  }

  &__link {
    display: block;
    color: var(--accent-color-2);
    font-size: 15px;
    font-weight: 400;
    text-align: left;
    margin-bottom: 50px;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 50px;
  }

  &__container-title {
    font-size: 21px;
    font-weight: 800;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 15px;
  }

  &__container-avatar {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    width: 200px;
    height: 120px;
    border-radius: 6px;
    background-color: #F2EFE9;

    .input-reset{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
    }
  }

  &__container-btn {
    padding: 0 12px;
    border-radius: 6px;
    min-height: 30px;
    background-color: #F2EFE9;
    font-size: 15px;
    font-weight: 400;
    color: var(--dark-color);
  }
}
