.category {

  &__inner {
    padding-top: 30px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-between;
    gap: 8px;
  }

  &__item {

    .image{
      width: 100%;
      height: 172px;
      border-radius: 6px;
    }
  }

  &__title {
    display: block;
    color: var(--dark-color);
    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
    margin-bottom: 15px;
  }

  &__nav{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    padding-top: 15px;
  }

  &__nav-item{
    width: 100%;
  }

  &__link {
    display: block;
    width: 100%;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;

    &:hover{
      color: var(--hover-color);
    }
  }
}
