.chatroom {
  max-width: 770px;

  &__inner {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    min-width: 770px;
    position: relative;
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: rgba(0, 0, 0, 0.13) 0px 24px 24px -24px;
    width: 100%;
    height: 64px;
    padding: 5px 10px 5px 28px;
  }

  &__menu{

    .list-reset{
      top: 60px;
      right: 20px;
      bottom: unset;
    }
  }

  &__header-content {
    display: flex;
    align-items: center;
    gap: 10px;

    & .image{
      border-radius: 50%;
      height: 40px;
    }
  }

  &__header-return {
    transform: rotate(-90deg);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;

    &:hover{
      background-color: #EBEAE8;
    }
  }

  &__header-user {
    display: flex;
    align-items: center;
    gap: 5px;

    & a{
      display: block;
      font-size: 16px;
      font-weight: 900;
      color: var(--dark-color);
      line-height: 24px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      max-width: 200px;

      &:hover{
        color: var(--hover-color);
      }
    }

    & time{
      font-size: 13px;
      font-weight: 500;
      line-height: 18px;
      color: var(--grey-color);
      cursor: default;
    }
  }

  &__header-item {
    display: block;
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
    line-height: 20px;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__header-menu {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;

    & a{
      font-size: 14px;
      font-weight: 500;
      line-height: 20px;
      color: var(--dark-color);
      cursor: pointer;
    }
  }

  &__header-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 13px;
    border-radius: 6px;
    min-height: 30px;
    background-color: #F2EFE9;
    font-size: 14px;
    font-weight: 500;
    color: var(--dark-color);

    &:hover{
      background-color: rgb(237, 233, 225);
    }

    &:active{
      background-color: rgb(229, 223, 211);
    }
  }

  &__container {
    min-height: 660px;
    max-height: 660px;
    padding: 30px 20px 30px 50px;
    overflow-y: scroll;
    overflow-x: hidden;

    &::-webkit-scrollbar-button{
      display: none;
    }

    &::-webkit-scrollbar{
      width: 6px;
      background-color: #FAFAFA;
    }

    &::-webkit-scrollbar-thumb{
      border-radius: 6px;
      background-color: #C5C5C5;

      &:hover{
        background-color: #969696;
      }
    }
  }

  &__date {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 12px 52px 24px;
    height: 54px;
    font-size: 14px;
    font-weight: 800;
    color: var(--dark-color);
    line-height: 18px;
    text-align: center;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 2px;
    width: 100%;
    min-height: 46px;
    margin-bottom: 15px;
  }

  &__sender {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-left: auto;

    .chatroom__message {
      position: relative;
      background-color: #E6F6FF;
      border-radius: 16px;
      padding: 11px 16px 13px 16px;
      min-height: 46px;
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      width: fit-content;
      max-width: 470px;

      .chatroom__message-info {
        display: flex;
        align-items: center;
        gap: 3px;
        position: absolute;
        left: -55px;
        bottom: 13px;
        font-size: 12px;
        font-weight: 500;
        color: var(--grey-color);
      }
    }
  }

  &__recipient {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-right: auto;

    .chatroom__message {
      position: relative;
      background-color: #F2F1F0;
      border-radius: 16px;
      padding: 11px 16px 13px 16px;
      min-height: 46px;
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      width: fit-content;
      max-width: 470px;

      .chatroom__message-info {
        display: flex;
        align-items: center;
        gap: 3px;
        position: absolute;
        right: -40px;
        bottom: 13px;
        font-size: 12px;
        font-weight: 500;
        color: var(--grey-color);
      }
    }
  }

  .nav {
    box-shadow: rgba(0, 0, 0, 0.13) 0px -24px 24px -24px;
    min-height: 72px;
    padding: 10px 30px;

    &__list {
      display: flex;
      align-items: center;
      gap: 5px;
    }

    &__btn {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 36px;
      height: 36px;
      border-radius: 50%;

      &:hover{
        background-color: #EBEAE8;
      }

      & .input-reset{
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        z-index: 2;
      }
    }

    &__input {
      display: block;
      height: 40px;
      max-width: 585px;
      width: 100%;
      padding: 9px 15px;
      border-radius: 6px;
      font-size: 16px;
      line-height: 22px;
      font-weight: 500;
      resize: none;

      &::-webkit-scrollbar{
        display: none;
      }

      &:focus{
        box-shadow: inset 0 0 0 3px #80d4ff;
      }
    }
  }

  .checked {
    fill: var(--accent-color-2);
  }

  &__dropdown {

    .list-reset{
      position: absolute;
      bottom: 70px;
      background-color: var(--light-color);
      box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 3px 0px, rgba(0, 0, 0, 0.12) 0px 4px 24px 0px;
      border-radius: 12px;
      padding: 16px 0 22px;
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      gap: 0;
      height: fit-content;
      z-index: 8;
      overflow: hidden;

      li{
        position: relative;

        .button{
          display: flex;
          align-items: center;
          justify-content: flex-start;
          gap: 5px;
          min-height: 40px;
          background-color: var(--light-color);
          color: var(--dark-color);
          font-size: 16px;
          font-weight: 400;
          padding: 8px 24px 10px;
          width: 100%;
          &:hover{
            background-color: #E5DFD3;
          }
          .image{
            border-radius: 50%;
          }
          .input-reset{
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            cursor: pointer;
          }
        }
      }
    }
  }
}

.modal-fast {
  width: 700px;

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    color: var(--dark-color);
    margin-bottom: 10px;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    color: var(--dark-color);
    line-height: 24px;
    margin: 0 0 35px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin-bottom: 20px;
  }

  &__item {
    position: relative;
    min-width: 600px;
    border-radius: 12px;
    padding: 18px 20px;
    background-color: var(--light-color);
    user-select: none;
    cursor: pointer;

    &:hover{
      background-color: #F7F7F5;

      & .modal-fast__item-menu{
        display: flex;
      }
    }
  }

  &__item-title {
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: var(--dark-color);
    margin-bottom: 10px;
    cursor: inherit;
  }

  &__item-text {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: var(--dark-color);
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    cursor: inherit;
    overflow: hidden;
  }

  &__item-menu {
    position: absolute;
    top: -15px;
    right: 20px;
    padding: 8px 10px;
    max-height: 30px;
    display: none;
    background-color: var(--light-color);
    -webkit-box-shadow: 0px 0px 12px 0px rgba(163,163,163,1);
    -moz-box-shadow: 0px 0px 12px 0px rgba(163,163,163,1);
    box-shadow: 0px 0px 12px 0px rgba(163,163,163,1);
    border-radius: 6px;

    .btn-reset:not(:last-child){
      margin-right: 10px;
      padding-right: 10px;
      border-right: 1px solid #ccc;
    }
  }

  &__btn{
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 0 13px;
    min-height: 40px;
    border-radius: 6px;
    background-color: #F2EFE9;
    font-size: 16px;
    font-weight: 400;
    color: var(--dark-color);

    &:hover{
      background-color: #ede9e1;
    }

    &:active{
      background-color: #e5dfd3;
    }
  }
}
.modal-ads {
  width: 470px;
  height: 680px;
  overflow-y: auto;

  &::-webkit-scrollbar{
    display: none;
  }

  &__title{
    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
    color: var(--dark-color);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 8px;
  }

  &__item {
    display: flex;
    align-items: stretch;
    gap: 15px;

    .image{
      border-radius: 6px;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__item-title {
    text-align: left;
    font-size: 13px;
    font-weight: 400;
    color: var(--dark-color);
    white-space: nowrap;
    max-width: 400px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__price {
    text-align: left;
    font-size: 13px;
    font-weight: 600;
    color: var(--grey-color);
    white-space: nowrap;
    max-width: 400px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.modal-control {

  &__title{
    font-size: 25px;
    font-weight: 700;
    color: var(--dark-color);
    line-height: 30px;
  }

  &__text{
    font-size: 16px;
    font-weight: 400;
    color: var(--dark-color);
    line-height: 24px;
  }

  .nav {

    &__list {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
    }

    &__item {

      &:nth-child(2) .btn-reset{
        background-color: #F2EFE9 !important;
        color: var(--dark-color) !important;

        &:hover{
          background-color: rgb(237, 233, 225) !important;
        }

        &:active{
          background-color: rgb(229, 223, 211) !important;
        }
      }

      .btn-reset{
        width: 100%;
        height: 40px;
        padding: 0 17px;
        border-radius: 6px;
        font-size: 16px;
        font-weight: 500;
        background-color: var(--accent-color-2);
        color: var(--light-color);
        min-width: 190px;

        &:hover{
          background-color: rgb(0, 153, 247);
        }

        &:active{
          background-color: rgb(0, 138, 237);
        }
      }
    }
  }
}
.small-btn{
  font-size: 14px !important;
  height: 32px !important;
  min-height: unset !important;
}
.active-dropdown-btn{
  background-image: url("../img/checked_2.svg");
  background-size: 16px;
  background-position-x: 90%;
  background-position-y: 50%;
  background-repeat: no-repeat;
  background-color: #E5DFD3 !important;
}
