.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.graph-modal {
  --transition-time: 0.3s;
  position: fixed;
  inset: 0;
  z-index: 1000;
	display: flex;
	padding: 30px 0;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container {
  position: relative;
  width: fit-content;
  margin: auto;
  display: block;
  background-color: #fff;
  border-radius: 28px;
  padding: 30px;
  cursor: default;
  display: none;
  max-height: calc(100vh - 100px);
  max-width: 95dvw;
}

@media (max-width: 1024px) {
  .graph-modal__container {
    width: 90%;
  }
}

.graph-modal__close {
  position: absolute;
  right: -40px;
  top: 0px;
  border: none;
  padding: 0;
  width: 20px;
  height: 20px;
  background-color: transparent;
  background-image: url("../img/svg/close.svg");
  cursor: pointer;
}

.graph-modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container.graph-modal-open {
  display: block;
}

.fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.travel {

  &__title {
    color: var(--dark-color);
    font-size: 25px;
    font-weight: 900;
    line-height: 30px;
    margin-bottom: 15px;
  }

  &__select {
    position: relative;
    min-height: 350px;
    margin-bottom: 10px;
  }

  &__input {
    min-height: 40px;
    min-width: 530px;
    border: 1px solid #ccc;
    padding: 0 15px;
    border-radius: 3px;
    font-size: 16px;
    font-weight: 500;
    color: var(--dark-color);

    &:focus{
      box-shadow: 0 0 1px 3px rgba(0, 170, 255, .3);
    }
  }

  &__content {
    position: absolute;
    top: 40px;
    left: 0;
    padding: 6px 0 8px;
    background-color: var(--light-color);
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    width: 100%;
    max-height: 300px;
    overflow-y: scroll;
    overflow-x: hidden;
    box-shadow: 0 1px 3px 0 #9b9b9b;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0 10px;
    min-height: 32px;
    width: 100%;
    background-color: var(--light-color);
    font-size: 16px;
    font-weight: 500;
    color: var(--dark-color);

    &:hover{
      background-color: #D6F1FF;
    }

    & span{
      color: #8f8f8f;
    }
  }

  &__submit {
    min-height: 40px;
    padding: 0 17px;
    border-radius: 6px;
    background-color: var(--accent-color-2);
    color: var(--light-color);
    font-size: 16px;
    font-weight: 500;

    &:hover{
      background-color: rgb(0, 153, 247);
    }

    &:active{
      background-color: rgb(0, 138, 237);
    }
  }
}

.date {
  padding: 10px 30px;
  min-width: 310px;
  user-select: none;

  &__nav {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 10px;
    padding: 0 8px;
  }

  &__nav-item {
    display: flex;
    align-items: center;

    &:nth-child(1){
      justify-content: flex-start;
    }
    &:nth-child(2){
      justify-content: center;
    }
    &:nth-child(3){
      justify-content: flex-end;
    }
  }

  &__table{
    width: 100%;

    & thead{
      margin-bottom: 5px;

      & tr{

        & th{

          & span{
            font-size: 11px;
            font-weight: 500;
            text-transform: uppercase;
          }
        }

      }
    }

    & tbody{

      & tr{

        & td{
          & button.btn-reset{
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 33px;
            min-height: 33px;
            color: var(--dark-color);
            font-size: 14px;
            font-weight: 400;

            &:hover{
              background-color: #D6F1FF;
            }
          }
        }
      }

    }

  }

  &__nav-svg {
    width: 14px;
    height: 20px;
  }

  &__nav-title {
    font-size: 14px;
    font-weight: 500;
  }

  &__clear{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    text-align: left;
    font-size: 14px;
    font-weight: 500;
    color: var(--accent-color-2);
    line-height: 20px;

    &:hover{
      color: var(--hover-color);
    }
  }
}
.special-week-day {
  color: red;
}
.none-date {
  opacity: .5;
  text-decoration: line-through;
  cursor: default;

  &:hover{
    background-color: var(--light-color) !important;
  }
}
.special-date {
  font-weight: 700;
}
.date-content{
  max-height: unset !important;
  overflow: hidden !important;
}

.modal-def{
  min-width: 630px;
  padding-bottom: 14px;

  &__title{
    font-size: 20px;
    font-weight: 700;
    line-height: 26px;
    color: var(--dark-color);
    margin: 0;
  }

  &__text{
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: var(--dark-color);
    margin: 0;

    span{
      color: var(--grey-color);
    }
  }

  &__menu{
    display: flex;
    align-items: center;
    gap: 10px;

    .btn-reset{
      display: flex;
      align-items: center;
      justify-content: center;
      width: fit-content;
      min-height: 40px;
      padding: 0 17px;
      border-radius: 6px;
      font-size: 16px;
      font-weight: 500;
      background-color: var(--accent-color-2);
      color: var(--light-color);
    }
  }
}

.modal-new {

  &__title {
    font-size: 25px;
    font-weight: 800;
    line-height: 34px;
    color: var(--dark-color);
    margin: 0;
  }

  &__input {
    display: block;
    height: 40px;
    width: 100%;
    border-radius: 3px;
    border: 1px solid rgb(204, 204, 204);
    padding: 0 10px;

    &:focus{
      box-shadow: rgb(207, 237, 255) 0px 0px 0px 3px;
    }
  }
}
