.slide {

  &__inner {
    padding-top: 50px;
  }

  &__title {
    display: block;
    font-size: 25px;
    font-weight: 700;
    color: var(--dark-color);
    line-height: 30px;
    margin-bottom: 20px;

    &:hover{
      color: var(--hover-color);
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 484px);
    justify-content: space-between;
    gap: 10px;
  }

  &__item {

    .image{
      min-width: 484px;
      height: 172px;
      border-radius: 6px;
    }
  }

  &__content {
    display: flex;
    align-items: flex-start;
    padding-top: 15px;
  }

  &__content-list {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 216px;
  }

  &__content-item {
    width: 100%;
  }

  &__content-link {
    display: block;
    width: 100%;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400px;
    line-height: 22px;

    &:hover{
      color: var(--hover-color);
    }
  }
}
