.header {
  position: relative;
  z-index: 50;
  min-height: 160px;

  .nav {
    position: relative;

    &__dropdown {
      position: absolute;
      top: 30px;
      left: 50%;
      transform: translateX(-50%);
      background-color: var(--light-color);
      width: 900px;
      box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 17px 0px;
      border-radius: 12px;
      padding: 36px 44px;
    }

    &__dropdown-title {
      font-size: 18px;
      font-weight: 800;
      line-height: 24px;
      color: var(--accent-color-2);
      cursor: pointer;
      margin-bottom: 10px;
      width: fit-content;

      &:hover{
        color: var(--hover-color);
      }
    }

    &__dropdown-list {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 20px;
    }
  }

  .dropdown {

    &__item {

      & .list-reset a{
        font-size: 13px;
        color: var(--accent-color-2);
        font-weight: 400;
        line-height: 20px;

        &:hover{
          color: var(--hover-color);
        }
      }
    }

    &__title {
      font-size: 16px !important;
      font-weight: 500 !important;
    }
  }

}

.header__wrapper {
  position: fixed;
  width: 100%;
  min-height: 110px;
  background: var(--light-color);
  padding-bottom: 40px;
}

.header-box-shadow {
  box-shadow: 0 10px 10px -10px rgba(0, 0, 0, .1);
}

.header__top {
  background: #292929;
  padding: 2px 0;
}

.header__top-inner {
  display: flex;
  align-items: center;
  min-height: 40px;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.menu__list {
  display: flex;
  align-items: center;
  gap: 22px;
}

.menu__link svg{
  color: #8F8F8F;

  &:hover{
    color: #B8B8B8;
  }

}

.dropdown-item {
  position: relative;
}

.dropdown-item:hover .dropdown {
  display: block;
}

.dropdown-link {
  display: flex;
  align-items: center;
  gap: 2px;
  fill: #fff;
}

.dropdown-link:hover+.menu__dropdown {
  display: block;
}

.menu__dropdown:hover {
  display: block;
}

.dropdown-link svg {
  rotate: 180deg;
}

.menu__link {
  text-decoration: none;
  line-height: 20px;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
}

.menu__item {
  display: flex;
  align-items: center;
  gap: 12px;
}

.menu__link:hover {
  color: var(--hover-color);
  fill: var(--hover-color);
}

.special-link {
  padding: 8px 14px;
  border-radius: 12px;
  background: #0099f7;
  font-size: 13px;
}

.special-link:hover {
  color: #fff;
  background: #008ee6;
}

.header__bottom {
  background: transparent;
}

.header__bottom-inner {
  display: flex;
  align-items: center;
  padding-top: 25px;

  .nav {
    &__list {
      display: flex;
      align-items: center;
      gap: 20px;
    }

    &__item{
      position: relative;
    }

    &__link {
      font-size: 14px;
      color: var(--dark-color);
      font-weight: 400;
      line-height: 0;

      &:hover {
        color: var(--hover-color);
      }
    }
  }
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-right: 20px;
  margin-bottom: 10px;
}

.header__search {
  display: flex;
  align-items: stretch;
  gap: 5px;
}

.header__btn {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 10px 17px;
  border-radius: 6px;
  background: #0099f7;
  color: #fff;
  white-space: nowrap;
}

.header__btn:hover {
  background: #008ee6;
}

.header__search-content {
  position: relative;
  display: flex;
  align-items: center;
  background: #0099f7;
  border-radius: 6px;
  margin-right: 25px;
}

.header__search-input {
  width: 715px;
  background: #ffffff;
  height: 40px;
  border-radius: 6px;
  border: 2px solid #0099f7;
  padding: 0 8px;

  &::placeholder {
    font-size: 14px;
  }
}

.header__search-btn {
  height: 40px;
  background: #0099f7;
  padding: 0 20px;
  border-radius: 6px;
  color: #fff;
  cursor: pointer;
}

.header__search-btn:hover {
  background: #008ee6;
}

.header__region {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  gap: 5px;
  background: transparent;
  font-size: 14px;
  color: #000;
  cursor: pointer;
}

.header__region span {
  white-space: nowrap;
}

.header__region:hover {
  color: var(--hover-color);
}

.dropdown {
  display: none;
  position: absolute;
  top: 20px;
  width: 130px;
  padding: 16px;
  border-top: 1px solid #dcdcdc;
  box-shadow: 0 5px 17px rgba(0, 0, 0, .15);
  background: #fff;
  border-radius: 4px;
  z-index: 2;
}

.dropdown__link {
  font-size: 14px;
  text-decoration: none;
  color: #0af;
}

.dropdown__link:hover {
  color: var(--hover-color);
}

.header {

  &__dropdown-list {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 40px;
    left: 0;
    background-color: var(--light-color);
    width: 715px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 17px 0px;
    border-radius: 12px;
    z-index: 9;
    padding: 20px 0;
    max-height: 50vh;
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__dropdown-item {
    height: 40px;
  }

  &__dropdown-link {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 40px;
    padding: 0 40px;
    font-size: 16px;
    font-weight: 500;
    color: var(--dark-color);
    width: 100%;
    background-color: var(--light-color);

    & .image {
      position: absolute;
      left: 10px;
    }

    &:hover {
      background-color: #F2EFE9;
    }
  }

  &__dropdown-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: -1;
    cursor: pointer;
  }
}
