.fixed-tab {
  margin-bottom: 30px;
  background: var(--light-color);

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    padding-left: 20px;
    width: 100%;
  }

  &__menu {
    position: relative;
    display: flex;
    align-items: center;
    gap: 30px;
  }

  &__type {
    display: flex;
    align-items: stretch;
    gap: 8px;
  }

  &__type-btn {
    min-width: 20px;
    min-height: 20px;
    background-image: url("../img/type.svg");
    background-size: cover;
    background-repeat: no-repeat;
    padding: 3px;
    border-radius: 3px;

    &:hover{
      background-color: #EBEAE8;
    }

    &:nth-child(1){
      background-position-x: 0;
    }

    &:nth-child(2){
      background-position-x: -30px;
    }

    &:nth-child(3){
      background-position-x: -60px;
    }
  }

  &__dropdown {
    position: absolute;
    bottom: -170px;
    left: 100px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    max-width: 160px;
    width: 100%;
    background-color: var(--light-color);
    z-index: 2;
    padding: 15px 0 20px;
    border-radius: 16px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 3px 0px, rgba(0, 0, 0, 0.12) 0px 4px 24px 0px;
  }

  &__dropdown-item {
    text-align: left;
    padding: 8px 15px;
    font-size: 15px;
    font-weight: 500;
    color: var(--dark-color);

    &:hover{
      background-color: #E5DFD3;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    color: var(--dark-color);
    padding: 3px 20px 3px 2px;
    border-radius: 3px;
    position: relative;

    &:hover{
      background-color: #EBEAE8;
    }

    &::after{
      content: '';
      position: absolute;
      top: 50%;
      right: 3px;
      transform: translateY(-50%) rotate(180deg);
      width: 16px;
      height: 16px;
      background-image: url("../img/arrow.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__switch {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__favorite {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 3px 2px;
    border-radius: 3px;
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    color: var(--dark-color);

    &:hover{
      background-color: #EBEAE8;
    }
  }

  &__favorite-svg {
    width: 16px;
    height: 16px;
  }
}

.active-dropdown-item {
  background-color: #E5DFD3;
  background-image: url("../img/checked_2.svg");
  background-repeat: no-repeat;
  background-size: 15px;
  background-position-y: 50%;
  background-position-x: 87%;
}

.fixed-tab-fixed{
  position: fixed;
  top: 0;
  min-width: 1025px;
  max-width: 1025px;
  padding: 12px 0;
  z-index: 10;
}
