.tool {

  &__inner {
    padding: 35px 0;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 8px;
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--dark-color);

    & span{
      text-align: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 18px;
      color: inherit;
    }

    &:hover{
      color: var(--hover-color);
    }
  }
}
