.work {

  &__inner{
    padding: 35px 0;
  }

  &__title{
    color: var(--dark-color);
    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
    margin: 0 0 20px;
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;

    & .image{
      max-width: 54px;
      max-height: 54px;
    }

    & span{
      color: var(--dark-color);
      font-size: 15px;
      font-weight: 500;
      text-align: center;
    }
  }

  .swiper-slide{
    display: grid;
    grid-template-columns: repeat(10, 80px);
    justify-content: space-between;
    align-items: stretch;
  }
}
