.search {
  &__inner {
    padding: 50px 0 0;
  }

  &__title {
    display: block;
    color: var(--dark-color);
    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
    margin-bottom: 20px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: stretch;
    gap: 5px;
  }

  &__item {
    background: #f2efe9;
    border-radius: 12px;
    height: 112px;
  }

  &__link {
    display: block;
    width: 100%;
    height: 100%;
    padding: 12px 14px 20px 16px;
  }

  &__item-title {
    display: block;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
  }

  &__item-text {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
  }
}
