.settings {
  max-width: 720px;

  &__inner {
    padding-top: 30px;
    padding-left: 20px;
  }

  &__title {
    margin: 0;
    font-size: 32px;
    font-weight: 800;
    line-height: 36px;
    color: var(--dark-color);
  }

  &__mail {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-bottom: 30px;
  }

  &__mail-btn {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 21px;
    font-weight: 500;
  }

  &__mail-alert {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__container-item {
    border-top: 1px solid #D6D6D6;
    padding-top: 20px;
  }

  &__phone {}

  &__phone-title {
    font-size: 18px;
    font-weight: 500;
    color: var(--dark-color);
    line-height: 24px;
  }

  &__phone-text {
    position: relative;
    margin: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    color: var(--dark-color);

    & svg{
      position: absolute;
      top: 0;
      left: -18px;
      fill: #04E061;
    }

    & a:hover{
      color: var(--hover-color);
    }
  }

  &__phone-content {
    display: flex;
    align-items: center;
    justify-content: space-between;

    & span{
      font-size: 14px;
      font-weight: 500;
      line-height: 18px;
      color: var(--grey-color);
    }
  }

  &__phone-num {
    font-weight: 400 !important;
    color: var(--dark-color) !important;
  }

  &__phone-btn {
    padding: 0 13px;
    min-height: 32px;
    border-radius: 6px;
    background-color: #F2EFE9;
    font-size: 14px;
    font-weight: 400;
    color: var(--dark-color);
  }

  &__bell {}

  &__bell-title {
    font-size: 21px;
    font-weight: 800;
    line-height: 26px;
    color: var(--dark-color);
  }

  &__text {
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    color: var(--dark-color);
    margin: 0;

    & a:hover{
      color: var(--hover-color);
    }
  }

  &__bell-subtitle {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    color: var(--dark-color);
    margin-bottom: 5px;
  }

  &__bell-content {
    position: relative;
    display: flex;
    align-items: center;
    gap: 5px;

    & div{
      margin-left: 10px;
    }
  }

  &__bell-btn {
    min-width: 165px;
    min-height: 44px;
    border-radius: 12px;
    background-color: #F2F1F0;
    padding: 12px 16px;
    text-align: left;
    font-size: 15px;
    font-weight: 500;
    color: #838383;
    border: 3px solid transparent;

    &:focus{
      border-color: var(--accent-color-2);
    }
  }

  &__social {}

  &__social-title {
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__social-content {
    display: grid;
    grid-template-columns: repeat(2, 200px);
    gap: 100px;
    align-items: center;
  }

  &__social-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__social-name {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
  }

  &__sales {

    .settings__text{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: fit-content;
      margin-left: auto;
    }
  }

  &__sales-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    max-width: 320px;
    margin-bottom: 15px;
  }

  &__custom {

    & .settings__text{
      font-size: 14px !important;
    }
  }

  &__custom-title {
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    color: var(--dark-color);
  }

  .switch{
    width: 40px !important;
    height: 20px !important;

    input:checked + .slider:before {
      -webkit-transform: translateX(20px) translateY(-50%);
      -ms-transform: translateX(14px) translateY(-50%);
      transform: translateX(14px translateY(-50%));
    }

    .slider{
      border-radius: 3px;
    }

    .slider::before{
      width: 15px;
      height: 15px;
      border-radius: 3px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 15px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__list-content {
    display: flex;
    flex-direction: column;
  }

  &__list-title {
    display: block;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    color: var(--dark-color);
  }

  &__list-text {
    display: block;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    color: var(--grey-color);
  }

  &__delete {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__delete-content {

    & .settings__text{
      font-size: 14px !important;
      font-weight: 500 !important;
      color: var(--grey-color) !important;
      max-width: 440px;
    }
  }

  &__delete-title {
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    color: var(--dark-color);
    margin-bottom: 10px;
  }

  &__delete-btn {
    font-size: 14px;
    font-weight: 400;
    color: var(--accent-color-2);

    &:hover{
      color: var(--hover-color);
    }
  }
}
.special-container{
  padding: 0;
}
.special-container .container{
  padding: 0 !important;
}
