.my-review {
  width: 770px;

  &__inner {
    padding-top: 30px;
  }

  &__title {
    margin: 0 0 20px;
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    color: var(--dark-color);
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    margin: 0;
  }

  &__list {
    padding-top: 20px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  &__item {
    position: relative;
    padding: 23px 23px 12px;
    border-radius: 20px;
    border: 1px solid #EBEBEB;

    &:hover .my-review__btn{
      display: block;
    }
  }

  &__description {
    display: flex;
    align-items: flex-start;
    gap: 10px;
    margin-bottom: 10px;

    & .my-review__text{
      font-size: 14px !important;
      line-height: 18px !important;
    }
  }

  &__picture {
    position: relative;

    & a:nth-child(1) .image{
      border-radius: 6px;
      overflow: hidden;
    }

    & a:nth-child(2){
      position: absolute;
      top: -10px;
      left: -10px;
      border-radius: 50%;
      overflow: hidden;
      border: 2px solid var(--light-color);
    }
  }

  &__list-user {
    font-size: 16px;
    font-weight: 700;
    color: var(--dark-color);

    &:hover{
      color: var(--hover-color);
    }
  }

  &__star {
    display: flex;
    align-items: center;
    gap: 5px;
    fill: #E0E0E0;

    &:hover{
      fill: #FFB020;
    }
  }

  &__btn {
    display: none;
    position: absolute;
    top: 23px;
    right: 23px;
  }

  .tabs {

    &__nav {
      display: flex;
      align-items: center;
    }

    &__nav-btn {
      padding: 0 12px;
      min-height: 40px;
      border-radius: 18px;
      color: var(--accent-color-2);

      &--active{
        background-color: #F5F5F5;
        color: var(--dark-color);
        font-weight: 700;
      }
    }

    &__panel {
      padding-top: 20px;
    }
  }
}
